/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React, { useState } from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql, Link } from "gatsby"
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer'
import Header from "./header"
import "./layout.css"
import 'bootstrap/dist/css/bootstrap.min.css';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ContactForm from "./contactForm"
import InstagramIcon from '@material-ui/icons/Instagram';
import FacebookIcon from '@material-ui/icons/Facebook';

const Layout = ({ children }) => {
  const iOS = process.browser && /iPad|iPhone|iPod/.test(navigator.userAgent);
  const [contactOpen, setContactOpen] = useState(false);
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
      <div style={{ minHeight: "100vh", display: "grid", gridAutoRows: "auto 1fr" }}>
        <Header siteTitle={data.site.siteMetadata.title} />

        <main style={{ display: "flex", maxWidth: "100%" }}>{children}</main>
        <footer style={{ width: "100%", background: "darkgrey", display: "flex", paddingTop: "30px", flexDirection: "column", alignItems: "center", borderTop: "1px solid white" }}>
          <div style={{ width: "80%", display: "flex", justifyContent: "space-between" }}>
            <section  >
              <h4 style={{ color: "white", width: "400px" }}>Vra tjnster:</h4>
              <nav>
                <ul style={{ width: "100%", marginLeft: "0", justifyContent: "space-evenly  ", listStyle: "none" }}>
                  <li>
                    <Link style={{ color: "white", fontWeight: "bold" }} to="/golvlaggning">Golvlggning</Link>
                  </li>
                  <li>
                    <Link style={{ color: "white", fontWeight: "bold" }} to="/golvslipning">Golvslipning</Link>
                  </li>
                  <li>
                    <Link style={{ color: "white", fontWeight: "bold" }} to="/mattlaggning">Mattlggning</Link>
                  </li>
                  <li>
                    <Link style={{ color: "white", fontWeight: "bold" }} to="/golvavjamning">Golvavjmning</Link>
                  </li>
                  <li>
                    <Link style={{ color: "white", fontWeight: "bold" }} to="/vattenskador">Vattenskador</Link>
                  </li>
                  <li>
                    <Link style={{ color: "white", fontWeight: "bold" }} to="/trapprenovering">Trapprenovering</Link>
                  </li>
                </ul>


              </nav>
            </section>
            <section style={{ color: "white", marginRight: "auto" }} >
              <h4 style={{ width: "400px" }}>Garanti och frskringar:</h4><br />
              <p style={{ color: "white", width: "300px" }}>Vi erbjuder tv till fem rs garanti beroende p vilket typ av golv du lgger.</p>
              <p style={{ color: "white", width: "300px" }}>Fr er och vr skerhet r vi allriskfrskrade hos Folksam.</p>
            </section>
            <section >
              <address style={{ color: "white", display: "flex", flexDirection: "column" }}>
                <h4>Kontakt</h4>
                <p style={{ color: "white" }}>Fretagsnummer:<br />
                  <a style={{ color: "white" }} href="tel:08-999017">08-999017</a>
                </p>

                <p style={{ color: "white" }}>Direktnummer:<br />
                  <a style={{ color: "white" }} href="tel:0732199908">0732199908</a><br />
                  <a style={{ color: "white" }} href="tel:0732199908">0732199908</a>
                </p>

                <p style={{ color: "white" }}>Epost:<br />
                  <a style={{ color: "white" }} href="mailto:kontakt@victoriagolv.se">info@victoriagolv.se</a><br />
                </p>
                <div style={{display:"flex", width:"100%",   padding:"0"}}>
                  <a style={{ color: "white" }} href="https://www.instagram.com/victoriagolvab/?hl=sv"><InstagramIcon /></a>
                  <FacebookIcon />
                </div>
              </address>
            </section>
          </div>

          <p style={{ color: "white" }}>Victoria Golv  {new Date().getFullYear()}</p>


        </footer>
      </div>

      {!contactOpen &&
        <aside onClick={() => setContactOpen(true)} style={{
          background: "darkgrey",
          display: "flex",
          position: "fixed",
          right: "0",
          zIndex: "10",
          top: "50%",
          borderRadius: "5% 0% 0% 5%",
          justifyContent: "space-between",
          alignItems: "center",
          padding: "10px 0 10px 0",
          height: "100px",
          border:"1px solid white"  
        }}>
          <ArrowBackIosIcon style={{color:"white"}} />
          <p style={{
            textAlign: "center",
            alignSelf: "center",
            lineHeight: 1,
            position: "relative",
            left: "-10px",
            fontSize: "x-large",
            color:"white"
          }}>Kontakta<br />oss</p>
        </aside>
      }
      <SwipeableDrawer disableBackdropTransition={!iOS} disableDiscovery={iOS} open={contactOpen} onClose={() => setContactOpen(false)} anchor={"right"}>

        <ContactForm />
      </SwipeableDrawer>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
