import PropTypes from "prop-types"
import React from "react"
import Nav from "./nav"
import { useStaticQuery, graphql, Link } from "gatsby"

const Header = ({ siteTitle }) => {

  const { gcms: { images } } = useStaticQuery(graphql`
  {
      gcms {
        images {
          logo {
            url
          }
        }
      }
  } 
  `)
  return (
    <header
      style={{
        background: `white`,
        display:"flex",
        flexDirection:"column",
        alignItems:"center"

      }}
    >
      <aside style={{ display: "flex", justifyContent: "center" }}>
      <Link to="/"><img height="150px" src={images[0].logo.url} alt="Logga" /></Link>
        
      </aside>
      <Nav />

    </header>
  )
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
